.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
}
.filter {
  margin-bottom: 20px;
}

.filter select {
  padding: 10px;
  font-size: 1rem;
  margin-left: 10px;
}
.search {
  margin-bottom: 20px;
}

.searchInput {
  padding: 10px;
  width: 100%;
  max-width: 400px;
  font-size: 16px;
  border: 1px solid #ddd;
  border-radius: 4px;
}